
.navbar {
  width: 100%;
  height: 50px;
  background: #262A33;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  z-index: 1000;
}
.navbar .nav-left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.navbar .nav-left .logo {
      width: 200px;
      height: 50px;
      background: url(../assets/logo.png) no-repeat 28px center;
      background-size: 84%;
      cursor: pointer;
}
.navbar .nav-left .lan {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      background: #363D4C;
      font-size: 12px;
      color: #fff;
      margin-left: 20px;
      cursor: pointer;
}
.navbar .nav-left .lan li {
        padding: 0 10px;
}
.navbar .nav-left .lan .active {
        background: #735DEE;
}
.navbar .nav-right {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 12px;
}
.navbar .nav-right .active {
      background: #735DEE;
}
.navbar .nav-right li {
      position: relative;
}
.navbar .nav-right li .navBarCode {
        width: 128px;
        height: 168px;
        background: #735DEE;
        position: absolute;
        left: 1px;
        padding: 2px;
        font-size: 12px;
        text-align: center;
        color: #3f3190;
}
.navbar .nav-right li .navBarCode img {
          width: 100%;
}
.navbar .nav-right li a {
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        height: 50px;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        color: #fff;
        padding: 0 20px;
        border-left: 1px solid #1B1D21;
        -webkit-transition: background .3s;
        transition: background .3s;
}
.navbar .nav-right li a:hover {
          background: #735DEE;
}
.navbar .nav-right li a:active {
          background: #4b2fe9;
}



